.contacts-list {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-top: 20px;

	.list-btns {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-bottom: 10px;

		a {
			text-decoration: none;
			color: white;
			margin-inline: 10px;
			font-size: 20px;
			font-weight: 600;
			transition: 0.3s;

			&:hover {
				box-shadow: 0px 2px 0px 0px rgba(255, 255, 255, 0.75);
			}
		}
	}

	.list {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		margin: 0 auto;
		justify-content: center;
	}
}
