.contacts-preview {
	height: 350px;
	width: 350px;
	padding: 20px;
	border: 1px solid white;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	margin: 10px;
	border-radius: 5px;

	.contact-img {
		width: 40px;
	}

	p {
		color: white;
		font-size: 20px;
	}

	a {
		text-decoration: none;
		color: white;
		font-size: 24px;
		font-weight: 700;
		transition: 0.3s;

		&:hover {
			box-shadow: 0px 2px 0px 0px rgba(255, 255, 255, 0.75);
		}
	}
}
