.app-header {
	background-color: hsla(240, 20%, 9%, 0.75);
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-inline: 20px;
	margin-bottom: 20px;

	img {
		width: 50px;
	}

	.nav-links {
		padding: 20px 0 20px;
		a {
			text-decoration: none;
			color: #bfbfce;
			font-size: 20px;
			font-weight: 500;
			text-transform: uppercase;
			padding: 20px;
			transition: 0.3s;

			&:hover {
				box-shadow: 0px -4px 0px 0px rgba(255, 255, 255, 0.75) inset;
			}
		}

		.active-nav {
			color: white;
			box-shadow: 0px -4px 0px 0px rgba(255, 255, 255, 0.75) inset;
		}
	}
}
