.contact-edit {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	label {
		color: #bfbfce;
		font-weight: 600;
		margin-block: 10px;
	}

	input {
		width: 300px;
		padding: 5px;
	}

	input:last-of-type {
		margin-bottom: 20px;
	}

	button {
		background-color: white;
		color: black;
		font-size: 14px;
		border-radius: 10px;
		padding: 10px 25px;
		border: none;
		outline: none;
		font-weight: 600;
		cursor: pointer;
		transition: 0.3s;

		&:hover {
			box-shadow: 0px 0px 10px 0px rgba(255, 255, 255, 0.61);
		}
	}
}
