.contact-details-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;

	.btn-go-back {
		background-color: white;
		color: black;
		font-size: 14px;
		border-radius: 10px;
		padding: 10px 25px;
		border: none;
		outline: none;
		align-self: flex-end;
		margin-bottom: 20px;
		margin-inline-end: 20px;
		font-weight: 600;
		cursor: pointer;
		transition: 0.3s;

		&:hover {
			box-shadow: 0px 0px 10px 0px rgba(255, 255, 255, 0.61);
		}
	}

	.contact-details {
		border: 1px solid white;
		border-radius: 10px;
		padding: 20px;
		width: 300px;
		display: flex;
		flex-direction: column;
		align-items: center;

		h1 {
			margin-bottom: 20px;
		}

		p {
			color: #bfbfce;
			margin-bottom: 10px;
			text-align: center;
		}

		p:last-of-type {
			margin-bottom: 30px;
		}

		.contact-btns {
			display: flex;
			justify-content: center;
			align-items: center;

			.btn-remove,
			.btn-edit {
				font-family: Arial, Helvetica, sans-serif;
				text-decoration: none;
				background-color: #fff;
				color: black;
				font-size: 14px;
				border-radius: 10px;
				padding: 10px 25px;
				border: none;
				outline: none;
				margin-inline: 10px;
				font-weight: 600;
				cursor: pointer;
				transition: 0.3s;

				&:hover {
					box-shadow: 0px 0px 10px 0px rgba(255, 255, 255, 0.61);
				}
			}
		}
	}
}
